<template>
    <div id="blog_list">
        <div class="list-group">
            <router-link v-bind:to="{path: '/blog_content_page/' + blog[index].writer + '/' + blog[index].blog_date}" class="list-group-item" v-for="(item, index) in blog" :key="item.id">
                <div class="panel-heading">
                    <h3 class="panel-title"><strong> :{{item.title}}</strong></h3>
                </div>
                <div class="panel-body">
                    : {{item.writer}} : {{item.blog_date | standard_date}}
                </div>
            </router-link>
            <div class="list-group-item" id="show_empty" v-show="empty">
                <h3></h3>
            </div>
        </div>  
    </div>
</template>

<script>
export default {
    name: "Search_blog",
    data: function () {
        return {
            blog: []
        }
    },
    created: function () {
        var search_keywords = document.URL.split('/').reverse()[0];
        var search_keywords = search_keywords.replace(/ /g, ".*");
        var pattem = ".*" + search_keywords + ".*";
        var post = {
            title: pattem
        }
        this.$http.post('./search_blog',post).then(function (res) {
            this.blog = res.data;
            this.blog = this.blog.reverse();
        })
    },
    watch: {
        $route (to, from) {
            if (from.name == "Search_blog" && to.name == "Search_blog") {
                var search_keywords = document.URL.split('/').reverse()[0];
                var search_keywords = search_keywords.replace(/ /g, ".*");
                var pattem = ".*" + search_keywords + ".*";
                var post = {
                    title: pattem
                }
                this.$http.post('./search_blog',post).then(function (res) {
                    this.blog = res.data;
                    this.blog = this.blog.reverse();
                })
            }
        }
    },
    filters: {
        standard_date: function (value) {
            return (new Date(parseInt(value))).toLocaleDateString();
        }
    },
    computed: {
        empty: function () {
            if (this.blog.length == 0)
                return true;
            else return false;
        }
    }
}
</script>

<style scoped>
    #blog_list {
        text-align: left;
    }
    #show_empty {
        text-align: center;
        background-color:beige;
    }
</style>



